import * as actionTypes from '../actions/actionTypes'
import { omit } from 'lodash'

export const initialState = {
    websites: [],
    title: null,
    header: {},
    footer: {},
    domain: null,
    bufferElements: [],
    pagesObjects: {},
    pagesStructure: [],
    _id: '',
    error: null,
    loading: false,
    pagesLoading: false,
    currentPage: '',
    urlNotValid: false,
}

const reducer = (state: Object = initialState, action: Object) => {
    switch (action.type) {
        case actionTypes.SAVE_WEBSITE_DATA_FROM_SERVER:
            return saveWebsiteDataFromServer(state, action)
        case actionTypes.LOAD_WEBSITE_FROM_SERVER:
            return loadWebsiteFromServer(state, action)

        case actionTypes.ADD_PAGE_SUCCESS:
            return addPageSuccess(state, action)
        case actionTypes.DELETE_PAGE_SUCCESS:
            return deletePageSuccess(state, action)
        case actionTypes.SET_CURRENT_PAGE:
            return setCurrentPage(state, action)
        case actionTypes.SAVE_PAGE_STRUCTURE_TO_STORE:
            return savePagesStructureToStore(state, action)
        case actionTypes.SAVE_PAGE_IN_STATE:
            return savePageInState(state, action)

        case actionTypes.ACTION_START:
            return actionStart(state)
        case actionTypes.ACTION_FAIL:
            return actionFail(state, action)
        case actionTypes.ACTION_SUCCESS:
            return actionSuccess(state)

        case actionTypes.ACTION_START_PAGE_LOAD:
            return actionStartPageLoad(state)
        case actionTypes.ACTION_SUCCESS_PAGE_LOAD:
            return actionSuccessPageLoad(state)

        case actionTypes.LOAD_GLOBALS_TO_STORE:
            return loadGlobalsToStore(state, action)

        case actionTypes.SET_URL_IS_NOT_VALID:
            return setUrlIsNotValid(state)

        default:
            return state
    }
}

const saveWebsiteDataFromServer = (state, action) => {
    let newState
    if (!action.data.website && !action.data.pagesObjects) {
        newState = initialState
    }

    return {
        ...state,
        websites: action.data.websites || state.websites,
        pagesObjects: action.data.pagesObjects || state.pagesObjects,
        ...action.data.website,
        error: null,
        loading: false,
        pagesLoading: false,
        ...newState,
    }
}

const loadWebsiteFromServer = (state, action) => ({
    ...initialState,
    ...action.data.website,
    pagesObjects: action.data.pagesObjects,
    websites: state.websites,
    error: null,
    loading: false,
    pagesLoading: false,
})

const addPageSuccess = (state, action) => ({
    ...state,
    pagesStructure: action.pagesStructure,
    pagesObjects: {
        ...state.pagesObjects,
        [action.page._id]: action.page,
    },
    currentPage: action.page._id,
    error: null,
    loading: false,
    pagesLoading: false,
})

const deletePageSuccess = (state, action) => {
    const newPagesObjects = { ...state.pagesObjects }
    for (let i in newPagesObjects) {
        const isDeleted = !action.pagesStructure.some(
            item => item.id.toString() === i.toString()
        )
        if (isDeleted) {
            delete newPagesObjects[i]
        }
    }
    return {
        ...state,
        pagesObjects: newPagesObjects,
        pagesStructure: action.pagesStructure,
        currentPage: action.currentPage,
        error: null,
        loading: false,
        pagesLoading: false,
    }
}

const actionStart = state => ({
    ...state,
    error: null,
    pagesLoading: true,
})

const actionSuccess = state => ({
    ...state,
    error: null,
    pagesLoading: false,
})

const actionStartPageLoad = state => ({
    ...state,
    error: null,
    loading: true,
})

const actionSuccessPageLoad = state => ({
    ...state,
    error: null,
    loading: false,
})

const actionFail = (state, action) => ({
    ...state,
    error: action.error,
    pagesLoading: false,
})

const setCurrentPage = (state, action) => ({
    ...state,
    currentPage: action._id,
    urlNotValid: false,
})

const savePagesStructureToStore = (state, action) => ({
    ...state,
    ...omit(action, ['type']),
    urlNotValid: false,
})

const savePageInState = (state, action) => ({
    ...state,
    pagesObjects: {
        ...state.pagesObjects,
        [state.currentPage]: {
            ...state.pagesObjects[state.currentPage],
            ...omit(action, ['type']),
        },
    },
})

const loadGlobalsToStore = (state, action) => ({
    ...state,
    pagesStructure: action.pagesStructure,
    pagesObjects: action.pagesObjects,
    currentPage: action.currentPage,
})

const setUrlIsNotValid = state => ({
    ...state,
    urlNotValid: true,
})

export default reducer
