import React, { Component } from 'react'
import type { Node } from 'react'

import classes from './NavigationItemButtonDropDown.module.css'
import Svg from '../../../../Svg/Svg'
import AUX from '../../../../../hoc/Aux'
import DropDownMenu from '../../../../../hoc/DropDownMenu/DropDownMenu'

type Props = {
    icon?: string,
    sideEffect?: () => {},
    width?: number,
    children?: Node,
    datatestid?: string,
}

type State = {
    isVisible: boolean,
}

export class NavigationItemButtonDropDown extends Component<Props, State> {
    state = {
        isVisible: false,
    }

    handleOpen = () => {
        this.setState({ isVisible: !this.state.isVisible })
        if (this.props.sideEffect) {
            this.props.sideEffect()
        }
    }

    handleClose = () => {
        this.setState({ isVisible: false })
    }

    render() {
        return (
            <AUX>
                <li
                    data-testid={this.props.datatestid || 'navItemBut'}
                    className={classes.NavigationItemButtonDropDown}
                    onClick={this.handleOpen}
                >
                    <Svg icon={this.props.icon} />
                </li>
                {this.state.isVisible ? (
                    <DropDownMenu
                        datatestid={'navItemButCover'}
                        close={this.handleClose}
                        isTopMenu
                        width={this.props.width}
                    >
                        {this.props.children}
                    </DropDownMenu>
                ) : null}
            </AUX>
        )
    }
}

export default NavigationItemButtonDropDown
