import React, { Component } from 'react'
import type { Node } from 'react'

import classes from './BigButtonDropDown.module.css'
import AUX from '../../../../hoc/Aux'
import DropDownMenu from '../../../../hoc/DropDownMenu/DropDownMenu'
import Svg from '../../../Svg/Svg'

type Props = {
    title: string,
    icon: string,
    children?: Node,
    doNotRebuild?: boolean,
}

type State = {
    isVisible: boolean,
}

class BigButtonDropDown extends Component<Props, State> {
    state = {
        isVisible: false,
    }

    handleOpen = () => {
        this.setState({ isVisible: !this.state.isVisible })
    }

    handleClose = () => {
        this.setState({ isVisible: false })
    }

    render() {
        return (
            <AUX>
                <button
                    data-testid="MyButton"
                    className={classes.Button}
                    onClick={this.handleOpen}
                >
                    <table className={classes.Table}>
                        <tbody>
                            <tr>
                                <td>
                                    <Svg icon={this.props.icon} />
                                </td>
                            </tr>
                            <tr>
                                <td>{this.props.title}</td>
                            </tr>
                        </tbody>
                    </table>
                </button>
                {this.props.doNotRebuild ? (
                    <DropDownMenu
                        close={this.handleClose}
                        customClass={
                            this.state.isVisible
                                ? classes.Visible
                                : classes.Hidden
                        }
                        doNotRebuild={this.props.doNotRebuild}
                    >
                        {this.props.children}
                    </DropDownMenu>
                ) : this.state.isVisible ? (
                    <DropDownMenu close={this.handleClose}>
                        {this.props.children}
                    </DropDownMenu>
                ) : null}
            </AUX>
        )
    }
}

export default BigButtonDropDown
