import React, { Component } from 'react'
import { connect } from 'react-redux'

import * as actions from '../../../../../../store/actions/index'
import Textarea from '../../../../../UI/Textarea/Textarea'

type Props = {
    page: {
        url: string,
        name: string,
        title: string,
        description: string,
    },
    websiteId: string,
    pagesStructure: Array<{}>,
    currentPageId: string,
    urlNotValid?: boolean,
    savePageInPagesStructure: (
        value: {},
        id: string,
        structure: Array<{}>,
        websiteId: string
    ) => {},
}

type State = {
    urlValue: string,
}

export class PageSettingsMenu extends Component<Props, State> {
    state = {
        urlValue: this.props.page.url,
    }

    render() {
        const { props } = this
        return (
            <table>
                <tbody>
                    <tr>
                        <td>Name</td>
                        <td>
                            <Textarea
                                datatestid="name"
                                startValue={props.page.name}
                                changed={value => {
                                    props.savePageInPagesStructure(
                                        { name: value },
                                        props.currentPageId,
                                        props.pagesStructure,
                                        props.websiteId
                                    )
                                }}
                            />
                        </td>
                    </tr>
                    <tr>
                        <td>URL</td>
                        <td>
                            <Textarea
                                datatestid="url"
                                startValue={this.state.urlValue}
                                changed={value => {
                                    props.savePageInPagesStructure(
                                        { url: value },
                                        props.currentPageId,
                                        props.pagesStructure,
                                        props.websiteId
                                    )
                                    this.setState({ urlValue: value })
                                }}
                                isWrong={props.urlNotValid}
                            />
                        </td>
                    </tr>
                    <tr>
                        <td>Title</td>
                        <td>
                            <Textarea
                                datatestid="title"
                                startValue={props.page.title}
                                changed={value =>
                                    props.savePageInPagesStructure(
                                        { title: value },
                                        props.currentPageId,
                                        props.pagesStructure,
                                        props.websiteId
                                    )
                                }
                            />
                        </td>
                    </tr>
                    <tr>
                        <td>Description</td>
                        <td>
                            <Textarea
                                datatestid="description"
                                startValue={props.page.description}
                                changed={value =>
                                    props.savePageInPagesStructure(
                                        { description: value },
                                        props.currentPageId,
                                        props.pagesStructure,
                                        props.websiteId
                                    )
                                }
                            />
                        </td>
                    </tr>
                </tbody>
            </table>
        )
    }
}

const mapStateToProps = state => {
    return {
        page:
            state.website.pagesStructure[
                state.website.pagesStructure.findIndex(
                    item => item.id === state.website.currentPage
                )
            ],
        websiteId: state.website._id,
        pagesStructure: [...state.website.pagesStructure],
        currentPageId: state.website.currentPage,
        urlNotValid: state.website.urlNotValid,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        savePageInPagesStructure: (
            newValue,
            currentPageId,
            pagesStructure,
            websiteId
        ) =>
            dispatch(
                actions.savePageInPagesStructure(
                    newValue,
                    currentPageId,
                    pagesStructure,
                    websiteId
                )
            ),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(PageSettingsMenu)
