import { updateChosenBoxesAndSectionValues } from '../utils'
import type { BuilderStateType } from '../builder'

export const chooseBox = (state: BuilderStateType, action: Object) => {
    if (state.chosenBoxes.includes(action.item)) {
        return state
    }

    let section = action.item
    const curState = { ...state }
    while (curState[section].type !== 'section') {
        section = curState[section].parent
    }
    const result = {
        ...state,
        chosenSection: section,
    }
    if (!action.ctrl || state.chosenBoxes.length === 0) {
        result.chosenBoxes = [action.item]
    } else {
        let prevSection = state.chosenBoxes[0]
        const curState = { ...state }
        while (curState[prevSection].type !== 'section') {
            prevSection = curState[prevSection].parent
        }
        if (prevSection !== section) {
            result.chosenBoxes = [action.item]
            result.chosenBoxesValues = { ...state[action.item] }
        } else {
            result.chosenBoxes = state.chosenBoxes.concat([action.item])
        }
    }
    // Update array of entered boxes
    result.enteredBoxes = []
    if (result.chosenBoxes.length > 0) {
        let box = result[result.chosenBoxes[0]].parent
        while (result[box].type !== 'section') {
            result.enteredBoxes.push(box)
            box = result[box].parent
        }
    } else {
        result.enteredBoxes = []
    }

    return updateChosenBoxesAndSectionValues(result)
}

export const enterBox = (state: BuilderStateType) => {
    const chosenBoxes = []
    if (
        state[state.chosenBoxes[0]].type === 'menu' ||
        state[state.chosenBoxes[0]].type === 'text'
    ) {
        chosenBoxes.push(state.chosenBoxes[0])
    }

    return updateChosenBoxesAndSectionValues({
        ...state,
        enteredBoxes: [state.chosenBoxes[0], ...state.enteredBoxes],
        chosenBoxes: chosenBoxes,
    })
}

export const exitBox = (state: BuilderStateType) => {
    let enteredBoxes = []
    let chosenBoxes = []
    if (state.enteredBoxes.length > 0) {
        const { parent } = state[state.enteredBoxes[0]]
        if (state[parent].type === 'box') {
            enteredBoxes = [parent]
        }
        chosenBoxes = [state.enteredBoxes[0]]
    }
    const result = {
        ...state,
        enteredBoxes,
        chosenBoxes,
    }
    return updateChosenBoxesAndSectionValues(result)
}
