import React, { Component } from 'react'
import type { Node } from 'react'

import classes from './DropDownMenu.module.css'

type Props = {
    width?: number,
    height?: number,
    datatestid?: string,
    close: Function,
    children?: Node,
    customClass?: string,
    doNotRebuild?: boolean,
}

class dropDownMenu extends Component<Props> {
    dropDownDiv: Object
    constructor(props: Props) {
        super(props)
        this.dropDownDiv = React.createRef()
    }

    setDropDownHeight = () => {
        //const topDifference = this.props.isTopMenu ? 0 : 56;
        const topDifference = 0
        const dropDown = this.dropDownDiv.current
        const buttonRect = dropDown.previousSibling.getBoundingClientRect()
        const height = document.documentElement
            ? document.documentElement.clientHeight
            : 0
        const width = document.documentElement
            ? document.documentElement.clientWidth
            : 0
        dropDown.style.top = buttonRect.bottom - topDifference + 'px'
        const rect = dropDown.getBoundingClientRect()
        if (rect.bottom > height) {
            if (
                height - (rect.bottom - rect.top) - (rect.bottom - rect.top) >
                10
            )
                dropDown.style.top =
                    height -
                    (rect.bottom - rect.top) -
                    (rect.bottom - rect.top) -
                    topDifference +
                    'px'
            else dropDown.style.height = height - rect.top + 'px'
        }
        //dropDown.style.width = rect.right - rect.left + 'px';
        if (rect.right > width) {
            if (width - (rect.right - rect.left) > 0)
                dropDown.style.left = width - (rect.right - rect.left) + 'px'
            else dropDown.style.left = '0px'
        }
    }

    componentDidMount() {
        if (!this.props.doNotRebuild) {
            this.setDropDownHeight()
        }
    }

    componentDidUpdate() {
        if (this.props.doNotRebuild) {
            this.setDropDownHeight()
        }
    }

    render() {
        return (
            <div
                className={
                    classes.DropDownMenuDiv +
                    ' ' +
                    (this.props.customClass || '')
                }
                style={{ minWidth: this.props.width || 0 + 'px' }}
                ref={this.dropDownDiv}
            >
                <div
                    data-testid={this.props.datatestid || 'dropDownCover'}
                    className={classes.Cover}
                    onClick={this.props.close}
                />
                <div className={classes.MenuDiv}>{this.props.children}</div>
            </div>
        )
    }
}

export default dropDownMenu
