import React, { Component } from 'react'
import { Route, Switch, withRouter, Redirect } from 'react-router-dom'
import { connect } from 'react-redux'

import Logout from './containers/Auth/Logout/Logout'
import './App.css'
import asyncComponent from './hoc/asyncComponent'
import * as actions from './store/actions/index'
import Auth from './containers/Auth/Auth'
import Layout from './components/Layout/Layout'
import LiveWebsite from './containers/LiveWebsite/LiveWebsite'
import PageOnLeaveSaveReminder from './containers/SiteBuilder/PageOnLeaveSaveReminder/PageOnLeaveSaveReminder'
import Aux from './hoc/Aux'

const asyncWebsiteDashboard = asyncComponent(() => {
    return import('./containers/WebsiteDashboard/WebsiteDashboard')
})

const asyncSiteBuilder = asyncComponent(() => {
    return import('./containers/SiteBuilder/SiteBuilder')
})

const asyncWebsiteSettings = asyncComponent(() => {
    return import('./containers/WebsiteSettings/WebsiteSettings')
})

const asyncUserSettings = asyncComponent(() => {
    return import('./containers/UserSettings/UserSettings')
})

type Props = {
    onTryAutoSignup: Object,
    isAuthenticated: boolean,
}

class App extends Component<Props> {
    componentDidMount() {
        this.props.onTryAutoSignup()
    }

    render() {
        let routes = (
            <Switch>
                <Route path="/login" component={Auth} />
                <Route path="/signup" component={Auth} />
                <Route path="/live" component={LiveWebsite} />
                <Redirect to="/login" />
            </Switch>
        )
        if (this.props.isAuthenticated) {
            routes = (
                <Aux>
                    <Layout>
                        <Switch>
                            <Route
                                path="/dashboard"
                                component={asyncWebsiteDashboard}
                            />
                            <Route
                                path="/editor"
                                component={asyncSiteBuilder}
                            />
                            <Route
                                path="/settings"
                                component={asyncWebsiteSettings}
                            />
                            <Route path="/user" component={asyncUserSettings} />
                            <Route path="/logout" component={Logout} />
                            <Route path="/live" component={LiveWebsite} />
                            <Redirect to="/editor" />
                        </Switch>
                    </Layout>
                    <PageOnLeaveSaveReminder />
                </Aux>
            )
        }

        return routes
    }
}

const mapStateToProps = state => {
    return {
        isAuthenticated: state.auth.token !== null,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onTryAutoSignup: () => dispatch(actions.authCheckState()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(App)
)
