import React from 'react'
import { connect } from 'react-redux'
import { Global } from '@emotion/core'

import * as actions from '../../../store/actions/index'
import * as classes from './Box.css.js'
import BoxControls from '../../../components/BuilderControls/BoxControls/BoxControls.js'
import getCombinedStyle from '../../../utils/getCombinedStyle'
import Text from './Text/Text'
import Menu from './Menu/Menu'

type Props = {
    box: {
        type: 'box' | 'text' | 'menu',
        children: Array<string>,
        parent: string,
        mapOn?: boolean,
        mapAddress?: string,
        mapZoom?: number,
        mapSatellite?: boolean,
        menuColorTopNormal: string,
        menuColorTopHover: string,
        menuColorTopActive: string,
        menuColorDDNormal: string,
        menuColorDDHover: string,
        menuColorDDActive: string,
        menuSpaceBetweenTopNormal: number,
        menuLeftPaddingTopNormal: number,
        menuTopPaddingTopNormal: number,
        menuRightPaddingTopNormal: number,
        menuBottomPaddingTopNormal: number,
        menuSpaceBetweenDDNormal: number,
        menuLeftPaddingDDNormal: number,
        menuTopPaddingDDNormal: number,
        menuRightPaddingDDNormal: number,
        menuBottomPaddingDDNormal: number,
        menuOrientation: 'horizontal' | 'vertical',
        menuXOffsetTopNormal: number,
        menuYOffsetTopNormal: number,
        menuWidthFlexibleTopNormal: boolean,
        menuWidthTopNormal: number,
        menuWidthFlexibleDDNormal: boolean,
        menuWidthDDNormal: number,
        menuHeightFlexibleTopNormal: boolean,
        menuHeightTopNormal: number,
        menuHeightFlexibleDDNormal: boolean,
        menuHeightDDNormal: number,
        menuFontSizeTopNormal: number,
        menuFontColorTopNormal: string,
        menuFontHighlightTopNormal: string,
        menuFontFamilyTopNormal: string,
        menuFontItalicTopNormal: boolean,
        menuFontUnderlineTopNormal: boolean,
        menuFontStrikethroughTopNormal: boolean,
        menuFontOverlineTopNormal: boolean,
        menuFontTransformTopNormal: string,
        menuLetterSpacingTopNormal: number,
        menuWordSpacingTopNormal: number,
        menuFontColorTopHover: string,
        menuFontHighlightTopHover: string,
        menuFontUnderlineTopHover: boolean,
        menuFontStrikethroughTopHover: boolean,
        menuFontOverlineTopHover: boolean,
        menuFontColorTopActive: string,
        menuFontHighlightTopActive: string,
        menuFontUnderlineTopActive: boolean,
        menuFontStrikethroughTopActive: boolean,
        menuFontOverlineTopActive: boolean,
        menuFontSizeDDNormal: number,
        menuFontColorDDNormal: string,
        menuFontHighlightDDNormal: string,
        menuFontFamilyDDNormal: string,
        menuFontItalicDDNormal: boolean,
        menuFontUnderlineDDNormal: boolean,
        menuFontStrikethroughDDNormal: boolean,
        menuFontOverlineDDNormal: boolean,
        menuFontTransformDDNormal: string,
        menuLetterSpacingDDNormal: number,
        menuWordSpacingDDNormal: number,
        menuFontColorDDHover: string,
        menuFontHighlightDDHover: string,
        menuFontUnderlineDDHover: boolean,
        menuFontStrikethroughDDHover: boolean,
        menuFontOverlineDDHover: boolean,
        menuFontColorDDActive: string,
        menuFontHighlightDDActive: string,
        menuFontUnderlineDDActive: boolean,
        menuFontStrikethroughDDActive: boolean,
        menuFontOverlineDDActive: boolean,
        menuFontHorizontalAlignTopNormal: string,
        menuFontHorizontalAlignTopHover: string,
        menuFontHorizontalAlignTopActive: string,
        menuFontHorizontalAlignDDNormal: string,
        menuFontHorizontalAlignDDHover: string,
        menuFontHorizontalAlignDDActive: string,
        menuFontVerticalAlignTopNormal: string,
        menuFontVerticalAlignTopHover: string,
        menuFontVerticalAlignTopActive: string,
        menuFontVerticalAlignDDNormal: string,
        menuFontVerticalAlignDDHover: string,
        menuFontVerticalAlignDDActive: string,
        menuColorTopBlockNormal: string,
        menuLeftPaddingTopBlockNormal: string,
        menuTopPaddingTopBlockNormal: string,
        menuRightPaddingTopBlockNormal: string,
        menuBottomPaddingTopBlockNormal: string,
        menuColorDDBlockNormal: string,
        menuLeftPaddingDDBlockNormal: string,
        menuTopPaddingDDBlockNormal: string,
        menuRightPaddingDDBlockNormal: string,
        menuBottomPaddingDDBlockNormal: string,
    },
    hoveredElement: string,
    activeElement: string,
    styles: Array<{
        type: 'hover' | 'active',
        influencer: string,
    }>,
    previewIsOn?: boolean,
    id: string,
    live?: boolean,
    handleChooseTextEditor: Function,
    deselectInner: (e: Event, id: string) => {},
    document: {},
}

const _Box = (props: Props) => {
    const determineStyleForState = el => {
        const boxShadow =
            (el.borderHOffset ? el.borderHOffset + 'px ' : '0px ') +
            (el.borderVOffset ? el.borderVOffset + 'px ' : '0px ') +
            (el.borderBlur ? el.borderBlur + 'px ' : '0px ') +
            (el.borderSize ? el.borderSize + 'px ' : '0px ') +
            (el.borderColor ? el.borderColor : 'rgba(0, 0, 0, 1)')

        let outline = !props.previewIsOn ? 'grey dashed 2px' : null
        if (props.id === props.hoveredElement) {
            outline = 'rgba(20, 160, 250, .8) solid 10px'
        }

        if (props.id === props.activeElement) {
            outline = 'rgba(250, 160, 20, .8) solid 10px'
        }

        return {
            userSelect: 'none',
            boxSizing: 'border-box',
            position: 'absolute',
            left: el.left,
            top: el.top,
            width: el.width,
            height: el.height,
            zIndex: el.zIndex,
            outline: !props.live ? outline : null,
            background: el.backgroundOn ? el.background : null,
            boxShadow: el.borderOn ? boxShadow : null,
        }
    }

    const el = props.box
    const styles = {}
    let map

    if (el) {
        const styleEl = getCombinedStyle(
            el,
            props.styles,
            props.hoveredElement,
            props.activeElement
        )
        let combinedStyle = determineStyleForState(styleEl)
        styles['#' + props.id + '_out'] = combinedStyle

        //  USE WHEN PREVIEW
        // props.styles.forEach( style => {
        //     styles['#' + style.influencer + ':' + style.type + ' #' + props.id + '_out']  = {
        //         left: style.left,
        //         top: style.top,
        //     };
        // });

        if (el.mapOn) {
            let src = 'https://www.google.com/maps/embed/v1/search'
            src += '?key=AIzaSyCUFx8cfHIz2hoYa49UDmZt2Ab1JqmUYZ4&q='
            src += el.mapAddress || 'The US'
            src += el.mapZoom ? '&zoom=' + el.mapZoom : ''
            src += el.mapSatellite ? '&maptype=satellite' : '&maptype=roadmap'
            map = (
                <iframe
                    title={props.id + '_map'}
                    style={classes.BoxMapIframe}
                    frameBorder="0"
                    src={src.replace(/ /g, '+')}
                />
            )
        }

        // let video;
        // if (el.video) {
        //     video = <ReactPlayer
        //         />;
        // }
    }

    const text =
        el.type === 'text' ? (
            <Text
                id={props.id}
                handleChooseTextEditor={props.handleChooseTextEditor}
                live={props.live}
                document={props.document}
            />
        ) : null

    const menu =
        el.type === 'menu' ? (
            <Menu
                id={props.id}
                live={props.live}
                document={props.document}
                box={props.box}
            />
        ) : null

    return (
        <div
            data-testid={`box_${props.id}_out`}
            id={props.id + '_out'}
            onMouseDown={
                !props.live ? e => props.deselectInner(e, props.id) : null
            }
        >
            {map}
            <div
                style={
                    el.type === 'menu'
                        ? { ...classes.BoxInner, overflow: 'visible' }
                        : classes.BoxInner
                }
                data-testid={`box_${props.id}_in`}
                id={props.id}
                name="keyElement"
            >
                {text}
                {menu}
                {el.children.map(box => (
                    <Box
                        key={box}
                        id={box}
                        handleChooseTextEditor={props.handleChooseTextEditor}
                        live={props.live}
                        document={props.document}
                    />
                ))}
            </div>
            {!props.live ? <BoxControls id={props.id} /> : null}
            <Global styles={styles} />
        </div>
    )
}

const mapStateToProps = (state, props) => {
    return {
        box: state.builder.present[props.id],
        hoveredElement: state.builder.present.hoveredElement,
        activeElement: state.builder.present.activeElement,
        styles: state.builder.present[props.id].styles.map(
            style => state.builder.present[style]
        ),
        previewIsOn: state.builder.present.preview,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        deselectInner: (e, item) => dispatch(actions.deselectInner(e, item)),
    }
}

const Box = connect(
    mapStateToProps,
    mapDispatchToProps
)(_Box)

export default Box
