// Combines style of the element including possible influences from hovered or active elements.
// Returns object with changed styles

export default (
    el: Object,
    styles: Array<Object>,
    hoveredElement: ?string,
    activeElement: ?string
): {
    mapOuter?: boolean,
    height?: number,
    outerBackgroundOn?: boolean,
    outerBackground?: string,
    backgroundOn?: boolean,
    background?: string,
    width?: number,
    left?: number,
    top?: number,
    zIndex?: number,
    borderOn?: boolean,
    borderSize?: number,
    borderHOffset?: number,
    borderVOffset?: number,
    borderBlur?: number,
    borderInset?: boolean,
    mapOn?: boolean,
    mapAddress?: string,
    mapZoom?: number,
    mapSatellite?: boolean,
    borderColor?: string,
    bottom?: number
    } => {
    let styleEl = { ...el }
    styles.forEach(style => {
        if (style.type === 'hover' && style.influencer === hoveredElement) {
            styleEl = {
                ...styleEl,
                ...style,
            }
        }
        if (style.type === 'active' && style.influencer === activeElement) {
            styleEl = {
                ...styleEl,
                ...style,
            }
        }
    })
    return styleEl
}
