/*
    This creates a line and highlights the element that is magnetized to on box drag
    It is used for both horizontal and vertical highlights
*/

import React from 'react'

import * as classes from './MagneticDragHighlight.css.js'
import AUX from '../../../../hoc/Aux'

type Props = {
    line: {
        width: number,
        height: number,
        left: number,
        top: number,
    },
    element: {
        width: number,
        height: number,
        left: number,
        top: number,
    },
    datatestid?: string,
}

export const MagneticDragHighlight = (props: Props) => {
    console.log(props)
    return (
        <AUX>
            <div
                data-testid={
                    props.datatestid
                        ? `${props.datatestid}_Line`
                        : 'magnetinLine'
                }
                style={{
                    ...classes.HighlightLine,
                    width: props.line.width + 'px',
                    height: props.line.height + 'px',
                    left: props.line.left + 'px',
                    top: props.line.top + 'px',
                }}
            />
            <div
                data-testid={
                    props.datatestid
                        ? `${props.datatestid}_Element`
                        : 'magnetinElement'
                }
                style={{
                    ...classes.HighlightElement,
                    width: props.element.width + 'px',
                    height: props.element.height + 'px',
                    left: props.element.left + 'px',
                    top: props.element.top + 'px',
                }}
            />
        </AUX>
    )
}

export default MagneticDragHighlight
