import axios from 'axios'

import * as actionTypes from './actionTypes'
import * as actions from './index'

export const authStart = () => ({
    type: actionTypes.AUTH_START,
})

export const authSuccess = (data: Object) => ({
    type: actionTypes.AUTH_SUCCESS,
    data,
})

export const authFail = (error: string) => ({
    type: actionTypes.AUTH_FAIL,
    error,
})

export const deleteStart = () => ({
    type: actionTypes.DELETE_START,
})

export const deleteSuccess = () => ({
    type: actionTypes.DELETE_SUCCESS,
})

export const deleteFail = (error: string) => ({
    type: actionTypes.DELETE_FAIL,
    error,
})

export const deleteUser = () => (dispatch: Object) => {
    dispatch(deleteStart())
    return axios
        .delete('/api/users')
        .then(response => {
            dispatch(logout())
            dispatch(deleteSuccess())
        })
        .catch(err => {
            dispatch(deleteFail(err.message))
        })
}

export const logout = () => {
    localStorage.removeItem('token')
    localStorage.removeItem('userId')
    localStorage.removeItem('currentAction')
    axios.defaults.headers.common['X-Auth-Token'] = null
    return {
        type: actionTypes.AUTH_LOGOUT,
    }
}

export const auth = (email: string, password: string, isSignup?: boolean) => (
    dispatch: Object
) => {
    dispatch(authStart())
    const authData = {
        email,
        password,
    }

    let url = '/api/users'
    if (!isSignup) {
        url = '/api/auth'
    }
    axios.defaults.headers.post['Content-Type'] = 'application/json'
    axios.defaults.headers.put['Content-Type'] = 'application/json'
    axios.defaults.headers.delete['Content-Type'] = 'application/json'
    axios.defaults.headers.common.Accept = 'application/json'

    return axios
        .post(url, JSON.stringify(authData))
        .then(response => {
            localStorage.setItem('token', response.data.token)
            localStorage.setItem('userId', response.data._id)
            localStorage.setItem('currentAction', response.data.currentAction)
            axios.defaults.headers.common['X-Auth-Token'] = response.data.token
            dispatch(authSuccess(response.data))
            dispatch(actions.saveWebsiteDataFromServer(response.data))
        })
        .catch(err => {
            dispatch(authFail(err.message))
        })
}

export const authCheckState = () => (dispatch: Object) => {
    const token = localStorage.getItem('token')
    const userId = localStorage.getItem('userId')
    axios.defaults.headers.post['Content-Type'] = 'application/json'
    axios.defaults.headers.put['Content-Type'] = 'application/json'
    axios.defaults.headers.delete['Content-Type'] = 'application/json'
    axios.defaults.headers.common.Accept = 'application/json'

    if (!token || !userId) {
        dispatch(logout())
    } else {
        axios.defaults.headers.common['X-Auth-Token'] = token
        dispatch(authStart())
        return axios
            .get('/api/users')
            .then(response => {
                localStorage.setItem(
                    'currentAction',
                    response.data.currentAction
                )
                dispatch(
                    authSuccess({
                        ...response.data,
                        _id: userId,
                        token,
                    })
                )
                dispatch(actions.saveWebsiteDataFromServer(response.data))
            })
            .catch(err => {
                dispatch(authFail(err.message))
            })
    }
}
