import React from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import classes from './Toolbar.module.css';
import Logo from '../../Logo/Logo';
import DrawerToggle from '../SideDrawer/DrawerToggle/DrawerToggle';
import NavigationItems from '../../SiteBuilderNavigation/Toolbar/NavigationItems/NavigationItems';
import * as actions from '../../../store/actions/index';

type Props = {
    drawerClicked: () => {},
    pageIsChanged?: boolean,
    markPageAsLeaving: () => {},
    location: Object
}

const toolbar = (props: Props) => {
    const handleDrawerToggleClick: Object = () => {
        props.drawerClicked();
        if(props.pageIsChanged) {
            props.markPageAsLeaving();
        };
    }
    return (
        <header className={classes.Toolbar}>
            <DrawerToggle clicked={handleDrawerToggleClick} />
            <div 
                className={classes.Logo}
                onClick={props.pageIsChanged ? () => props.markPageAsLeaving() : null}>
                <Logo />
            </div>
            { props.location.pathname === '/editor' ?
                <div className={classes.BuilderMenu}>
                    <NavigationItems />
                </div>
                : null
            }
        </header>
    )
};

const mapStateToProps = state => {
    return {
        pageIsChanged: state.builder.present.changed
    }
};

const mapDispatchToProps = dispatch => {
    return {
        markPageAsLeaving: () => dispatch(actions.markPageAsLeaving()),
    }
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(toolbar));