import React from 'react';
import { connect } from 'react-redux';

import * as classes from './ControlSectionDrag.css.js';

type Props = {
    chosenSection: string,
    id: string,
    previewIsOn?: boolean,
}

export const ControlSectionDrag = (props: Props) => {
    return !props.previewIsOn ? 
        <div data-testid={`sectionDrag_${props.id}`} style={props.chosenSection === props.id ? {...classes.SectionDrag, background: 'green'} : classes.SectionDrag }></div>
        : null;
};

const mapStateToProps = state => {
    return {
        chosenSection: state.builder.present.chosenSection,
        previewIsOn: state.builder.present.preview        
    };
};

export default connect(mapStateToProps)(ControlSectionDrag);