import axios from 'axios'
import { ActionCreators as UndoActionCreators } from 'redux-undo'

import * as actionTypes from './actionTypes'
import * as actions from './index'
import { determineCurrentPage } from '../../utils/determineCurrentPage'

type PagesStructureType = Array<{
    id: string,
    isNotPublished: boolean,
    isHomePage: boolean,
}>

export const saveWebsiteDataFromServer = (data: Object) => ({
    type: actionTypes.SAVE_WEBSITE_DATA_FROM_SERVER,
    data,
})

export const loadWebsiteFromServer = (data: Object) => ({
    type: actionTypes.LOAD_WEBSITE_FROM_SERVER,
    data,
})

export const createWebsite = () => (dispatch: Object) => {
    dispatch(actionStart())
    return axios
        .post('/api/websites')
        .then(response => {
            const currentPage = determineCurrentPage(response.data)
            dispatch(
                saveWebsiteDataFromServer({ ...response.data, currentPage })
            )
            dispatch(
                actions.loadCurrentPageToBuilder(
                    currentPage,
                    response.data.pagesObjects
                )
            )
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const loadWebsite = (_id: string) => (dispatch: Object) => {
    dispatch(actionStart())
    return axios
        .get(`/api/websites/${_id}`)
        .then(response => {
            const currentPage = determineCurrentPage(response.data)
            dispatch(loadWebsiteFromServer({ ...response.data, currentPage }))
            dispatch(
                actions.loadCurrentPageToBuilder(
                    currentPage,
                    response.data.pagesObjects
                )
            )
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const saveWebsiteSettings = (
    _id: string,
    title: string,
    domain: string
) => (dispatch: Object) => {
    dispatch(actionStart())
    const websiteData = {
        title,
        domain,
    }
    return axios
        .put(`/api/websites/${_id}`, JSON.stringify(websiteData))
        .then(response => {
            dispatch(saveWebsiteDataFromServer(response.data))
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const deleteWebsite = (_id: string) => (dispatch: Object) => {
    dispatch(actionStart())
    return axios
        .delete(`/api/websites/${_id}`)
        .then(response => {
            const currentPage = determineCurrentPage(response.data)
            dispatch(
                saveWebsiteDataFromServer({ ...response.data, currentPage })
            )
            dispatch(
                actions.loadCurrentPageToBuilder(
                    currentPage,
                    response.data.pagesObjects
                )
            )
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const actionStart = () => ({
    type: actionTypes.ACTION_START,
})

export const actionSuccess = () => ({
    type: actionTypes.ACTION_SUCCESS,
})

export const actionStartPageLoad = () => ({
    type: actionTypes.ACTION_START_PAGE_LOAD,
})

export const actionSuccessPageLoad = () => ({
    type: actionTypes.ACTION_SUCCESS_PAGE_LOAD,
})

export const actionFail = (error: string) => ({
    type: actionTypes.ACTION_FAIL,
    error,
})

export const addPage = (websiteId: string, currentPageId: string) => (
    dispatch: Object
) => {
    dispatch(actionStart())
    return axios
        .post(
            '/api/pages',
            JSON.stringify({
                websiteId,
                currentPageId,
            })
        )
        .then(response => {
            dispatch(addPageSuccess(response.data))
            dispatch(
                actions.loadCurrentPageToBuilder(
                    response.data.page._id,
                    null,
                    response.data.page
                )
            )
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const addPageSuccess = (data: Object) => ({
    type: actionTypes.ADD_PAGE_SUCCESS,
    page: data.page,
    pagesStructure: data.pagesStructure,
})

export const deletePage = (currentPageId: string, pagesObjects: Object) => (
    dispatch: Object
) => {
    dispatch(actionStart())
    return axios
        .delete(`/api/pages/${currentPageId}`)
        .then(response => {
            dispatch(deletePageSuccess(response.data))
            dispatch(
                actions.loadCurrentPageToBuilder(
                    response.data.currentPage,
                    pagesObjects
                )
            )
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const deletePageSuccess = (data: Object) => ({
    type: actionTypes.DELETE_PAGE_SUCCESS,
    pagesStructure: data.pagesStructure,
    currentPage: data.currentPage,
})

export const duplicatePage = (websiteId: string, currentPageId: string) => (
    dispatch: Object
) => {
    dispatch(actionStart())
    return axios
        .post(
            '/api/pages',
            JSON.stringify({
                currentPageId,
                websiteId,
                duplicate: true,
            })
        )
        .then(response => {
            dispatch(addPageSuccess(response.data))
            dispatch(
                actions.loadCurrentPageToBuilder(
                    response.data.page._id,
                    null,
                    response.data.page
                )
            )
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const savePagesStructure = (
    _id: string,
    pagesStructure: PagesStructureType
) => (dispatch: Object) => {
    dispatch(actionStart())
    return axios
        .put(`/api/websites/${_id}`, JSON.stringify({ pagesStructure }))
        .then(response => {
            dispatch(actionSuccess())
            if (response.data.urlNotOk) {
                dispatch(setUrlIsNotValid())
            } else {
                dispatch(savePagesStructureToStore(pagesStructure))
            }
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const setUrlIsNotValid = () => ({
    type: actionTypes.SET_URL_IS_NOT_VALID,
})

export const savePagesStructureToStore = (
    pagesStructure: PagesStructureType
) => ({
    type: actionTypes.SAVE_PAGE_STRUCTURE_TO_STORE,
    pagesStructure,
})

export const setCurrentPageMain = (
    _id: string,
    pagesObjects: Object,
    websiteId: string
) => (dispatch: Object) => {
    dispatch(actionStartPageLoad())
    dispatch(setCurrentPage(_id))
    dispatch(actions.loadCurrentPageToBuilder(_id, pagesObjects))
    return axios
        .put(
            `/api/websites/currentpage/${websiteId}`,
            JSON.stringify({ currentPage: _id })
        )
        .then(response => {
            dispatch(actionSuccessPageLoad())
            // $FlowFixMe
            dispatch(UndoActionCreators.clearHistory())
        })
        .catch(err => {
            dispatch(actionFail(err.message))
            // $FlowFixMe
            dispatch(UndoActionCreators.clearHistory())
        })
}

export const setCurrentPage = (_id: string) => ({
    type: actionTypes.SET_CURRENT_PAGE,
    _id,
})

export const savePage = (
    value: Object,
    currentPageId: string,
    pagesStructure: PagesStructureType
) => (dispatch: Object) => {
    dispatch(actionStart())
    dispatch(savePageInState(value))
    const currentPage = pagesStructure.find(item => item.id === currentPageId)
    if (currentPage) {
        currentPage.isNotPublished = true
    }
    dispatch(actions.markPageAsSaved())
    return axios
        .put(
            `/api/pages/${currentPageId}`,
            JSON.stringify({ ...value, pagesStructure })
        )
        .then(response => {
            dispatch(savePagesStructureToStore(pagesStructure))
            dispatch(actionSuccess())
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const savePageInPagesStructure = (
    value: Object,
    currentPageId: string,
    pagesStructure: PagesStructureType,
    websiteId: string
) => (dispatch: Object) => {
    const index = pagesStructure.findIndex(item => item.id === currentPageId)
    if (index < 0) {
        return
    }
    pagesStructure[index] = {
        ...pagesStructure[index],
        ...value,
    }

    return dispatch(savePagesStructure(websiteId, pagesStructure))
}

export const savePageInPagesStructureHomePage = (
    value: Object,
    currentPageId: string,
    pagesStructure: PagesStructureType,
    websiteId: string
) => (dispatch: Object) => {
    pagesStructure.forEach(item => (item.isHomePage = false))
    return dispatch(
        savePageInPagesStructure(
            value,
            currentPageId,
            pagesStructure,
            websiteId
        )
    )
}

export const publishPage = (
    websiteId: string,
    currentPageId: string,
    pagesStructure: PagesStructureType,
    publishOne: boolean
) => (dispatch: Object) => {
    dispatch(actionStart())
    if (publishOne) {
        let currentPage = pagesStructure.find(item => item.id === currentPageId)
        if (currentPage) {
            currentPage.isNotPublished = false
        }
    } else {
        pagesStructure.forEach(item => (item.isNotPublished = false))
    }
    return axios
        .post(
            '/api/pages/publish',
            JSON.stringify({
                websiteId,
                currentPageId,
                publishOne,
                pagesStructure,
            })
        )
        .then(response => {
            dispatch(actionSuccess())
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const revertPage = (
    websiteId: string,
    currentPageId: string,
    pagesStructure: PagesStructureType,
    publishOne: boolean
) => (dispatch: Object) => {
    dispatch(actionStart())
    if (publishOne) {
        let currentPage = pagesStructure.find(item => item.id === currentPageId)
        if (currentPage) {
            currentPage.isNotPublished = false
        }
    } else {
        pagesStructure.forEach(item => (item.isNotPublished = false))
    }
    return axios
        .post(
            '/api/pages/revert',
            JSON.stringify({
                websiteId,
                currentPageId,
                publishOne,
                pagesStructure,
            })
        )
        .then(response => {
            dispatch(saveWebsiteDataFromServer(response.data))
            dispatch(
                actions.loadCurrentPageToBuilder(
                    currentPageId,
                    response.data.pagesObjects
                )
            )
        })
        .catch(err => {
            dispatch(actionFail(err.message))
        })
}

export const savePageInState = (value: Object) => ({
    type: actionTypes.SAVE_PAGE_IN_STATE,
    ...value,
})

export const loadGlobalsToStore = (
    pagesStructure: PagesStructureType,
    pagesObjects: Object,
    currentPage: string
) => ({
    type: actionTypes.LOAD_GLOBALS_TO_STORE,
    pagesStructure,
    pagesObjects,
    currentPage,
})
