import omit from 'object.omit'

import { updateChosenBoxesAndSectionValues } from '../utils'
import type { BuilderStateType } from '../builder'

export const setStateHover = (state: BuilderStateType, action: Object) => {
    let element
    if (action.isSection) {
        element = state.chosenSection
    } else if (state.chosenBoxes.length > 0) {
        element = state.chosenBoxes[0]
    }
    return updateChosenBoxesAndSectionValues({
        ...state,
        hoveredElement: element
            ? element === state.hoveredElement
                ? null
                : element
            : null,
        activeElement: null,
    })
}

export const setStateActive = (state: BuilderStateType, action: Object) => {
    let element
    if (action.isSection) {
        element = state.chosenSection
    } else if (state.chosenBoxes.length > 0) {
        element = state.chosenBoxes[0]
    }
    return updateChosenBoxesAndSectionValues({
        ...state,
        hoveredElement: null,
        activeElement: element
            ? element === state.activeElement
                ? null
                : element
            : null,
    })
}

export const unsetStateHoverActive = (state: BuilderStateType) =>
    updateChosenBoxesAndSectionValues({
        ...state,
        hoveredElement: null,
        activeElement: null,
    })

export const clearStyle = (state: BuilderStateType, action: Object) => {
    const influencer = state.hoveredElement || state.activeElement
    const type = state.hoveredElement ? 'hover' : 'active'
    let newState = { ...state }
    let path

    if (!influencer) {
        return
    }
    if (action.isSection) {
        if (!state.chosenSection) {
            return
        }
        path = `${state.chosenSection}_${influencer}_${type}`
        newState = {
            ...newState,
            [path]: {
                ...omit(newState[path], action.value),
            },
        }
    } else {
        newState.chosenBoxes.forEach(box => {
            path = `${box}_${influencer}_${type}`
            newState = {
                ...newState,
                [path]: {
                    ...omit(newState[path], action.value),
                },
            }
        })
    }
    return updateChosenBoxesAndSectionValues({
        ...newState,
        changed: true,
    })
}
