import React from 'react';
import { connect } from 'react-redux';
import { omit } from 'lodash'

import * as classes from './PageOnLeaveSaveReminder.module.css';
import * as actions from '../../../store/actions/index';
import LoginButton from '../../../components/UI/LoginButton/LoginButton.js';

type Props = {
    pageIsChanged: boolean,
    isLeavingBuilder: boolean,
    currentPageId: string,
    pagesStructure: Array<{}>,
    currentPageState: {},
    savePage: (newValue: {}, currentPageId: string, pagesStructure: Array<{}>) => {},
    markPageAsSaved: () => {}
}

const PageOnLeaveSaveReminder = (props: Props) => {
    return ( props.pageIsChanged && props.isLeavingBuilder ?
        <div data-testid="pagesOnLeave" 
            className={classes.Cover}>
            <div className={classes.Main}>
                You have made changes on this page. If you don't send the changes they will be lost. Do you want to save the changes?
                <LoginButton
                    clicked={() => props.markPageAsSaved()}
                    btnType="Danger" >
                    Do not save changes
                </LoginButton>
                <LoginButton
                    clicked={() => props.savePage(
                        {content: props.currentPageState}, 
                        props.currentPageId,
                        props.pagesStructure)}
                    btnType="Success" >
                    Save changes
                </LoginButton>
            </div>
        </div> : null
    );
};

PageOnLeaveSaveReminder.propTypes = {
};

const mapStateToProps = state => {
    return {
        pageIsChanged: state.builder.present.changed,
        isLeavingBuilder: state.builder.present.leaving,
        currentPageId: state.website.currentPage,
        pagesStructure: state.website.pagesStructure,
        currentPageState: omit(state.builder.present, [
            'pageZoom',
            'alignMode',
            'saveHistory',
            'chosenBoxes',
            'chosenSection',
            'chosenBoxesValues',
            'chosenSectionValue',
            'chosenTextValues',
            'enteredBoxes',
            'hoveredElement',
            'activeElement',
            'changedStyleValues',
            'boxesDragStartValues',
            'preview',
            'changed',
            'leaving'
        ])
    };
};

const mapDispatchToProps = dispatch => {
    return {
        savePage: (newValue, currentPageId, pagesStructure) => dispatch(actions.savePage(newValue, currentPageId, pagesStructure)),
        markPageAsSaved: () => dispatch(actions.markPageAsSaved())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PageOnLeaveSaveReminder);

