import React from 'react'
import ReactDOM from 'react-dom'
import { BrowserRouter } from 'react-router-dom'
import { Provider } from 'react-redux'
import { createStore, applyMiddleware, compose, combineReducers } from 'redux'
import thunk from 'redux-thunk'
import axios from 'axios'

import './index.css'
import App from './App'
//import * as serviceWorker from './serviceWorker';
import builderReducer from './store/reducers/BuilderReducer/builder'
import topMenuReducer from './store/reducers/topMenu'
import bufferReducer from './store/reducers/buffer'
import authReducer from './store/reducers/auth'
import websiteReducer from './store/reducers/website'
import { logout } from './store/actions/index'

const composeEnhancers =
    process.env.NODE_ENV === 'development'
        ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
        : null || compose

if (process.env.NODE_ENV === 'development') {
    axios.defaults.baseURL = 'http://localhost:5000'
}

export const rootReducer = combineReducers({
    builder: builderReducer,
    topMenu: topMenuReducer,
    buffer: bufferReducer,
    auth: authReducer,
    website: websiteReducer,
})

export const store = createStore(
    rootReducer,
    //composeEnhancers(applyMiddleware(thunk))
    applyMiddleware(thunk)
)

// set axios interceptors
axios.interceptors.request.use(function(config) {
    if (localStorage.getItem('currentAction')) {
        config.headers.currentAction = localStorage.getItem('currentAction')
    }
    return config
})

axios.interceptors.response.use(
    function(response: { data: mixed }) {
        if (!response.data.currentAction) {
            localStorage.setItem(
                'currentAction',
                (parseInt(localStorage.getItem('currentAction')) + 1).toString()
            )
        }
        return response
    },
    function(error) {
        if (error.response) {
            if (parseInt(error.response.status) === 412) {
                store.dispatch(logout())
            }
        }
        return Promise.reject(error)
    }
)

// const store = createStore(rootReducer,
//          applyMiddleware(thunk)
//      );

const app = (
    <Provider store={store}>
        <BrowserRouter>
            <App />
        </BrowserRouter>
    </Provider>
)

const root = document.getElementById('root')
if (root !== null) {
    ReactDOM.render(app, root)
}

