export const builderZoomOut = (state: Object) => ({
  ...state,
  pageZoom: state.pageZoom > 10 ? state.pageZoom - 10 : state.pageZoom,
});

export const builderZoomIn = (state: Object) => ({
  ...state,
  pageZoom: state.pageZoom + 10,
});

export const builderZoomReset = (state: Object) => ({
  ...state,
  pageZoom: 100,
});

export const pagePreview = (state: Object) => {
    return {
        ...state,
        preview: !state.preview,
}};
