import * as actionTypes from './actionTypes'
import * as actions from './index'
import { getElementsToAdd } from '../reducers/BuilderReducer/utils'

export const chooseBox = (item: string, ctrl?: boolean) => (
    dispatch: Object
) => {
    dispatch(chooseBoxDo(item, ctrl))
    dispatch(actions.setActiveTopMenuItem('boxes'))
}

export const chooseBoxDo = (item: string, ctrl?: boolean) => ({
    type: actionTypes.CHOOSE_BOX,
    item,
    ctrl,
})

export const chooseSection = (item: string, event: Event) => {
    event.stopPropagation()
    return (dispatch: Object) => {
        dispatch(chooseSectionDo(item))
        dispatch(actions.setActiveTopMenuItem('sections'))
    }
}

export const chooseSectionDo = (item: string) => ({
    type: actionTypes.CHOOSE_SECTION,
    item,
})

export const deselectElements = () => ({ type: actionTypes.DESELECT_ELEMENTS })
export const deselectInner = (e: Event, item: string) => {
    e.stopPropagation()
    return { type: actionTypes.DESELECT_INNER, item }
}

export const changeElementPropertyValue = (
    value: any,
    key: string,
    isSection?: boolean,
    isNotForHistory?: boolean
) => ({
    type: actionTypes.CHANGE_ELEMENT_PROPERTY_VALUE,
    value,
    key,
    isSection,
    isNotForHistory,
})

export const changeElementPropertyValues = (
    isNotForHistory?: boolean,
    items: Array<string>
) => ({
    type: actionTypes.CHANGE_ELEMENT_PROPERTY_VALUES,
    items,
    isNotForHistory,
})

export const deleteSection = () => ({ type: actionTypes.DELETE_SECTION })
export const addSection = () => ({ type: actionTypes.ADD_SECTION })
export const duplicateSection = () => ({ type: actionTypes.DUPLICATE_SECTION })
export const moveUpSection = () => ({ type: actionTypes.MOVE_UP_SECTION })
export const moveDownSection = () => ({ type: actionTypes.MOVE_DOWN_SECTION })

export const deleteBox = () => ({ type: actionTypes.DELETE_BOX })
export const addBox = (rootElements?: Array<string>, items?: Object) => ({
    type: actionTypes.ADD_BOX,
    rootElements,
    items,
})
export const duplicateBox = () => ({ type: actionTypes.DUPLICATE_BOX })
export const moveUpBox = () => ({ type: actionTypes.MOVE_UP_BOX })
export const moveDownBox = () => ({ type: actionTypes.MOVE_DOWN_BOX })
export const zIndexUpBox = () => ({ type: actionTypes.ZINDEX_UP_BOX })
export const zIndexDownBox = () => ({ type: actionTypes.ZINDEX_DOWN_BOX })

export const copyBox = (builderPresentState: Object, bufferState: Object) => (
    dispatch: Object
) => {
    const firstLayerElements = []
    const elementsToAdd = getElementsToAdd(
        builderPresentState,
        builderPresentState.chosenBoxes,
        firstLayerElements,
        null,
        undefined,
        null,
        bufferState
    )
    dispatch(actions.addElementsToBuffer(firstLayerElements, elementsToAdd))
}

export const cutBox = (builderPresentState: Object, bufferState: Object) => (
    dispatch: Object
) => {
    const firstLayerElements = []
    const elementsToAdd = getElementsToAdd(
        builderPresentState,
        builderPresentState.chosenBoxes,
        firstLayerElements,
        null,
        undefined,
        null,
        bufferState
    )
    dispatch(actions.addElementsToBuffer(firstLayerElements, elementsToAdd))
    dispatch(deleteBox())
}

export const pasteBox = (builderPresentState: Object, bufferState: Object) => (
    dispatch: Object
) => {
    const firstLayerElements = []
    const elementsToAdd = getElementsToAdd(
        bufferState,
        bufferState.rootElements,
        firstLayerElements,
        null,
        undefined,
        null,
        builderPresentState
    )
    dispatch(addBox(firstLayerElements, elementsToAdd))
}

