import React, { Component } from 'react'
import { connect } from 'react-redux'
import { DragDropContext } from 'react-dnd'
import HTML5Backend from 'react-dnd-html5-backend'
import Sortly from 'react-sortly'

import * as actions from '../../../../../../../store/actions/index'
import Svg from '../../../../../../Svg/Svg'
import withDragDropContext from '../../../../../../../hoc/withDragDropContext'

type Props = {
    setCurrentPageMain: (
        _id: string,
        pagesObjects: {},
        websiteId: string
    ) => {},
    savePagesStructure: (_id: string, pagesStructure: Array<{}>) => {},
    websiteId: '',
    pagesObjects: {},
    currentPage: string,
    items: Array<{}>,
}

class PagesMenuList extends Component<Props> {
    handleChange = items => {
        this.props.savePagesStructure(this.props.websiteId, items)
    }

    render() {
        const ItemRenderer = props => {
            const {
                name,
                path,
                connectDragSource,
                connectDropTarget,
                isDragging,
                isClosestDragging,
                id,
                isHomePage,
                isHidden,
                isNotPublished,
            } = props
            const style = {
                position: 'relative',
                border: '1px solid #ccc',
                cursor: 'move',
                padding: 10,
                paddingRight: 30,
                marginBottom: 4,
                ...(isDragging || isClosestDragging ? { opacity: 0.3 } : null),
                margin: 10,
                marginLeft: 10 + path.length * 50,
                width: 300,
                outline:
                    this.props.currentPage === id ? '1px solid blue' : null,
            }

            const iconsContainerStyle = {
                position: 'absolute',
                top: 6,
                right: 6,
                fill: '#555',
            }

            const el = (
                <div
                    data-testid={id}
                    style={style}
                    onMouseDown={() =>
                        this.props.setCurrentPageMain(
                            id,
                            this.props.pagesObjects,
                            this.props.websiteId
                        )
                    }
                >
                    {name}
                    <div style={iconsContainerStyle}>
                        {isHomePage ? (
                            <Svg
                                datatestid={`home_${id}`}
                                icon='<svg height="20" viewBox="0 0 24 24" width="20"><path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z"></path></svg>'
                            />
                        ) : null}
                        {isHidden ? (
                            <Svg
                                datatestid={`hid_${id}`}
                                icon='<svg height="20" viewBox="0 0 24 24" width="20"><path d="M12 7c2.76 0 5 2.24 5 5 0 .65-.13 1.26-.36 1.83l2.92 2.92c1.51-1.26 2.7-2.89 3.43-4.75-1.73-4.39-6-7.5-11-7.5-1.4 0-2.74.25-3.98.7l2.16 2.16C10.74 7.13 11.35 7 12 7zM2 4.27l2.28 2.28.46.46C3.08 8.3 1.78 10.02 1 12c1.73 4.39 6 7.5 11 7.5 1.55 0 3.03-.3 4.38-.84l.42.42L19.73 22 21 20.73 3.27 3 2 4.27zM7.53 9.8l1.55 1.55c-.05.21-.08.43-.08.65 0 1.66 1.34 3 3 3 .22 0 .44-.03.65-.08l1.55 1.55c-.67.33-1.41.53-2.2.53-2.76 0-5-2.24-5-5 0-.79.2-1.53.53-2.2zm4.31-.78l3.15 3.15.02-.16c0-1.66-1.34-3-3-3l-.17.01z"></path></svg>'
                            />
                        ) : null}
                        {isNotPublished ? (
                            <Svg
                                datatestid={`pub_${id}`}
                                icon='<svg height="20" viewBox="0 0 24 24" width="20"><path xmlns="http://www.w3.org/2000/svg" d="M5 4v2h14V4H5zm0 10h4v6h6v-6h4l-7-7-7 7z"></path></svg>'
                            />
                        ) : null}
                    </div>
                </div>
            )
            return connectDragSource(connectDropTarget(el))
        }

        return (
            <Sortly
                items={this.props.items}
                itemRenderer={ItemRenderer}
                onChange={this.handleChange}
            />
        )
    }
}

const mapStateToProps = state => {
    return {
        websiteId: state.website._id,
        pagesObjects: state.website.pagesObjects,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setCurrentPageMain: (_id, pagesObjects, websiteId) =>
            dispatch(actions.setCurrentPageMain(_id, pagesObjects, websiteId)),
        savePagesStructure: (_id, pagesStructure) =>
            dispatch(actions.savePagesStructure(_id, pagesStructure)),
    }
}

export default withDragDropContext(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(PagesMenuList)
)
