import type { BuilderStateType } from './builder'

//This utility function updates values for chosen section and boxes.
//It includes values for normal state. hovered and active states.
//For boxes it also generates drag start values
export const updateChosenBoxesAndSectionValues = (newState: Object) => {
    const styledElement = newState.hoveredElement || newState.activeElement
    const boxesDragStartValues = []

    newState.chosenBoxesValues = {}
    newState.chosenBoxes.forEach(box => {
        mergeValuesOfChosenBoxes(newState[box], newState.chosenBoxesValues)
        boxesDragStartValues.push({
            box,
            width: newState[box].width,
            height: newState[box].height,
            left: newState[box].left,
            right: newState[box].right,
            top: newState[box].top,
            bottom: newState[box].bottom,
        })
    })

    const chosenBoxesValuesStyled = {}
    if (styledElement) {
        const endPath = newState.hoveredElement
            ? `_${styledElement}_hover`
            : `_${styledElement}_active`

        newState.chosenBoxes.forEach((box, index) => {
            const fullPath = box + endPath
            if (newState[fullPath]) {
                mergeValuesOfChosenBoxes(
                    newState[fullPath],
                    chosenBoxesValuesStyled
                )
                boxesDragStartValues[index] = {
                    ...boxesDragStartValues[index],
                    ...newState[fullPath],
                }
            }
        })
    }
    newState.chosenBoxesValues = {
        ...newState.chosenBoxesValues,
        ...chosenBoxesValuesStyled,
    }
    newState.boxesDragStartValues = boxesDragStartValues

    newState.chosenSectionValue = { ...newState[newState.chosenSection] }

    if (styledElement) {
        const style =
            newState[
                `${newState.chosenSection}_${styledElement}_${
                    newState.hoveredElement ? 'hover' : 'active'
                }`
            ]
        if (newState[style]) {
            newState.chosenSectionValue = {
                ...newState.chosenSectionValue,
                ...newState[style],
            }
        }
    }
    return newState
}

export const mergeValuesOfChosenBoxes = (source: Object, result: Object) => {
    for (const attr in source) {
        if (result[attr] === undefined) {
            result[attr] = source[attr]
        } else if (result[attr] !== source[attr]) {
            result[attr] = false
        }
    }
    return result
}

export const changeElementPropertyValue = (state: Object, action: Object) => {
    const updatedElements = {}

    const getUpdatedElement = (state, action, el) => {
        if (!state.hoveredElement && !state.activeElement) {
            updatedElements[el] = { ...state[el] }
            updatedElements[el][action.key] = action.value
        } else {
            const key = state.hoveredElement
                ? `${el}_${state.hoveredElement}_hover`
                : `${el}_${state.activeElement}_active`
            if (!state[el].styles.includes(key)) {
                updatedElements[el] = {
                    ...state[el],
                    styles: [...state[el].styles, key],
                }
            }
            updatedElements[key] = {
                ...state[key],
                influencer: state.hoveredElement,
                styleType: state.hoveredElement ? 'hover' : 'active',
            }
            updatedElements[key][action.key] = action.value
        }
    }

    if (!action.isSection) {
        state.chosenBoxes.forEach(box => {
            getUpdatedElement(state, action, box)
        })
    } else {
        getUpdatedElement(state, action, state.chosenSection)
    }

    return updateChosenBoxesAndSectionValues({
        ...state,
        changed: true,
        ...updatedElements,
    })
}

export const changeElementPropertyValues = (state: Object, action: Object) => {
    const updatedElements = {}

    const getUpdatedElement = (state, el, values) => {
        if (!state.hoveredElement && !state.activeElement) {
            updatedElements[el] = { ...state[el] }
            values.forEach(value => {
                updatedElements[el][value.key] = value.value
            })
        } else {
            const key = state.hoveredElement
                ? `${el}_${state.hoveredElement}_hover`
                : `${el}_${state.activeElement}_active`
            if (!state[el].styles.includes(key)) {
                updatedElements[el] = {
                    ...state[el],
                    styles: [...state[el].styles, key],
                }
            }
            updatedElements[key] = {
                ...state[key],
                influencer: state.hoveredElement,
                styleType: state.hoveredElement ? 'hover' : 'active',
            }
            values.forEach(value => {
                updatedElements[key][value.key] = value.value
            })
        }
    }

    if (!action.isSection) {
        action.items.forEach(item => {
            getUpdatedElement(state, item.item, item.values)
        })
    }

    return updateChosenBoxesAndSectionValues({
        ...state,
        changed: true,
        ...updatedElements,
    })
}

export const deselectElements = (state: Object) =>
    updateChosenBoxesAndSectionValues({
        ...state,
        chosenBoxes: [],
        chosenSection: null,
        enteredBoxes: [],
        chosenBoxesValues: {},
    })

export const deselectInner = (state: Object, action: Object) => {
    const enteredBoxes = []
    if (state[action.item].type !== 'section') {
        enteredBoxes.push(action.item)
        let box = state[action.item].parent
        while (state[box].type !== 'section') {
            enteredBoxes.push(box)
            box = state[box].parent
        }
    }
    const chosenBoxes = []
    if (enteredBoxes.length > 0) {
        if (['text', 'menu'].includes(state[enteredBoxes[0]].type)) {
            chosenBoxes.push(enteredBoxes[0])
        }
    }
    return updateChosenBoxesAndSectionValues({
        ...state,
        chosenBoxes: chosenBoxes,
        enteredBoxes: enteredBoxes,
    })
}

export const generateIncomeIds = (
    state: BuilderStateType,
    elements: Array<string>,
    incomeIds: Array<string>
) => {
    elements.forEach(el => {
        incomeIds.push(el)
        generateIncomeIds(state, state[el].children, incomeIds)
    })
}

export const getElementsToAdd = (
    state: BuilderStateType,
    elements: Array<string>,
    firstLayerElements: ?Array<string>,
    newElementsObj: Object,
    parent?: { children: Array<string> },
    parentId?: ?string,
    stateId?: ?{ currentId: string },
    idsObj: Object
) => {
    if (firstLayerElements) {
        const incomeIds = []
        generateIncomeIds(state, elements, incomeIds)
        idsObj = {}
        incomeIds.forEach(id => {
            let newId
            if (!stateId) {
                newId = `element_${state.currentId}`
                state.currentId += 1
            } else {
                newId = `element_${stateId.currentId}`
                stateId.currentId += 1
            }
            idsObj[id] = newId
        })
        newElementsObj = {}
    }
    elements.forEach(element => {
        const newId = idsObj[element]
        const styles = []
        state[element].styles.forEach(style => {
            const styleObj = {
                ...state[style],
                influencer:
                    idsObj[state[style].influencer] || state[style].influencer,
            }
            const newStyleId = `${newId}_${styleObj.influencer}_${
                styleObj.styleType
            }`
            styles.push(newStyleId)
            newElementsObj[newStyleId] = styleObj
        })

        const newElement = {
            ...state[element],
            styles,
            children: [],
        }
        if (firstLayerElements) {
            firstLayerElements.push(newId)
        } else if (parent) {
            newElement.parent = parentId
            parent.children.push(newId)
        }
        newElementsObj[newId] = newElement
        getElementsToAdd(
            state,
            state[element].children,
            null,
            newElementsObj,
            newElement,
            newId,
            stateId,
            idsObj
        )
    })
    if (firstLayerElements) {
        return newElementsObj
    }
}

export const getElementsToDelete = (elements: Array<string>, state: Object) => {
    let toDelete = []
    elements.forEach(element => {
        toDelete.push(element)
        toDelete = toDelete.concat(
            getElementsToDelete(state[element].children, state)
        )
    })
    return toDelete
}
