import { initialState } from '../builder'
import type { BuilderStateType } from '../builder'

export const loadCurrentPageToBuilder = (
    state: BuilderStateType,
    action: Object
) => {
    if (action._id && (action.pagesObjects || action.pageObject)) {
        const content = action.pagesObjects
            ? action.pagesObjects[action._id].content
            : action.pageObject.content
        return {
            ...initialState,
            ...content,
        }
    }
}

export const markPageAsSaved = (state: BuilderStateType) => ({
    ...state,
    changed: false,
    leaving: false,
})

export const markPageAsLeaving = (state: BuilderStateType) => ({
    ...state,
    leaving: true,
})
