import React from 'react'
import { connect } from 'react-redux'
import type { Node } from 'react'

import classes from './ToolsSet.module.css'

type Props = {
    activeTopMenuItem: 'sections' | 'boxes' | 'text',
    children: Node,
    title: 'sections' | 'boxes' | 'text',
    isActive: ?boolean,
}

export const ToolsSet = (props: Props) => {
    return (
        <table
            data-testid="table"
            className={
                props.activeTopMenuItem === props.title || props.isActive
                    ? [classes.ToolsSet, classes.active].join(' ')
                    : classes.ToolsSet
            }
        >
            <tbody>
                <tr>{props.children}</tr>
            </tbody>
        </table>
    )
}

const mapStateToProps = state => {
    return {
        activeTopMenuItem: state.topMenu.activeTopMenuItem,
    }
}

export default connect(mapStateToProps)(ToolsSet)
