import { updateChosenBoxesAndSectionValues } from '../utils'
import type { BuilderStateType } from '../builder'

export const chooseSection = (state: BuilderStateType, action: Object) =>
    updateChosenBoxesAndSectionValues({
        ...state,
        chosenSection: action.item,
        chosenBoxes: [],
        chosenBoxesValues: {},
        enteredBoxes: [],
    })
