import omit from 'object.omit'
import type { BuilderStateType } from '../builder'

import {
    updateChosenBoxesAndSectionValues,
    getElementsToAdd,
    getElementsToDelete,
} from '../utils'

export const addSection = (state: BuilderStateType) => {
    const newId = `element_${state.currentId}`
    state.currentId += 1
    const newSection = {
        type: 'section',
        height: 200,
        background: 'rgba(200, 100, 30)',
        header: false,
        footer: false,
        allPages: false,
        space: 50,
        children: [],
        styles: [],
    }
    return updateChosenBoxesAndSectionValues({
        ...state,
        [newId]: newSection,
        chosenBoxes: [],
        chosenSection: newId,
        enteredBoxes: [],
        sectionsOnPage: state.sectionsOnPage.concat([newId]),
        changed: true,
    })
}

export const duplicateSection = (state: BuilderStateType) => {
    if (!state.chosenSection) {
        return
    }
    const firstLayerElements = []
    const elementsToAdd = getElementsToAdd(
        state,
        [state.chosenSection],
        firstLayerElements
    )

    return updateChosenBoxesAndSectionValues({
        ...state,
        ...elementsToAdd,
        chosenBoxes: [],
        enteredBoxes: [],
        chosenSection: firstLayerElements[0],
        sectionsOnPage: state.sectionsOnPage.concat(firstLayerElements),
        changed: true,
    })
}

export const deleteSection = (state: BuilderStateType) => {
    if (!state.chosenSection) {
        return
    }
    const elementsToRemove = getElementsToDelete([state.chosenSection], state)
    let elementsToRemoveWithStyles = []
    elementsToRemove.forEach(element => {
        elementsToRemoveWithStyles = elementsToRemoveWithStyles.concat(
            [element],
            state[element].styles
        )
        if (state.hoveredElement === element) {
            state.hoveredElement = null
        }
        if (state.activeElement === element) {
            state.activeElement = null
        }
    })
    const newState = omit(state, elementsToRemoveWithStyles)
    newState.sectionsOnPage = newState.sectionsOnPage.filter(
        el => el !== state.chosenSection
    )
    return updateChosenBoxesAndSectionValues({
        ...newState,
        chosenSection: null,
        chosenBoxes: [],
        enteredBoxes: [],
        changed: true,
    })
}
